import { BaseService } from "../glue";
import { OutputModel } from "../models/output";
class OutputService extends BaseService {
  state;
  constructor() {
    super("output");
    this.state = new OutputModel();
  }
  append = (value) => {
    this.dispatch((draft) => {
      draft.value += value;
    });
  };
}
export {
  OutputService
};
